<template>
  <div>
    <div class="index-header">
      <div class="header">
        <Com-Header/>
      </div>
      <div class="slide">
        <img src="../assets/images/wisdomNewSer/fuwu_banner@2x.png" alt>
      </div>
    </div>

    <div class="detail-block-2 bg-white">
      <div class="detail-header">
        <p class="title">
          面向多样化的无人服务场景，
          <br>从0到1打造新物种
        </p>
        <p class="sub-title">
          面向智慧零售，提供从端到云，从硬件到软件，
          <br>从生产到运维的整体解决方案
        </p>
      </div>

      <div class="margin-top-50">
        <img src="../assets/images/wisdomNewSer/img_changjing@2x.png" alt>
      </div>
    </div>

    <div class="seize-seat"></div>

    <div class="detail-block-2 bg-white">
      <div class="detail-header">
        <p class="title">"新物种"诞生之路</p>
      </div>

      <div class="margin-top-40">
        <img src="../assets/images/wisdomNewSer/img_weilai@2x.png" alt>
      </div>
    </div>
    <div class="seize-seat"></div>

    <div class="detail-block-2 bg-white">
      <div class="detail-header">
        <p class="title">典型案例</p>
      </div>

      <div class="case-content">
        <ul class="box">
          <li @click="goNext(item)" v-for="(item,index) in caseData" :key="index">
            <div>
              <img :src="item.url" alt>
            </div>
            <div class="detail">
              <div class="desc">
                <p v-text="item.imgTitle" class="case-sub-title"></p>
                <p v-text="item.title" class="title"></p>
              </div>
              <div class="pv">
                <div>浏览量 {{item.pv}}</div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>

    <Com-Footer/>
  </div>
</template>

<script>
import ComHeader from "../components/ComHeader";
import ComFooter from "../components/ComFooter";

export default {
  name: "",
  data() {
    return {
      caseData: []
    };
  },
  components: { ComHeader, ComFooter },

  computed: {},

  mounted() {
    this.getCaseData();
  },
  methods: {
    //  获取案例数据
    getCaseData() {
      var that = this;
      fetch(this.API.proType)
        .then(response => response.json())
        .then(function(res) {
          console.log(JSON.stringify(res, null, 2));
          if (res.code === "0000") {
            that.caseData = res.detail;
          }
        })
        .catch(e => console.log("Oops, error", e));
    },
    // 跳转案例详情
    goNext(item) {
      this.SendPv(item);
      this.$router.push({
        path: "/caseDetail",
        query: {
          type: item.suffix
        }
      });
    },
    // 发送PV数据
    SendPv(item) {
      fetch(
        this.API.proPv + item.index,
      {
           method: "PUT",
      })
        .then(response => response.json())
        .then(function(res) {})
        .catch(e => console.log("Oops, error", e));
    }
  }
};
</script>

<style scoped  lang="less">
.slide {
  height: 11.5rem;
  font-size: 0;
  img {
    width: 100%;
  }
}
.index-header {
  position: relative;
  .header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
  }
}

.detail-block-2 {
  padding: 0.4rem;
  padding-top: 0.6rem;
  .detail-header {
    .title {
      font-size: 0.36rem;
      color: #3f4a82;
      letter-spacing: 0.69px;
    }
    .sub-title {
      font-size: 0.28rem;
      color: #4f5f7b;
      letter-spacing: 0;
      margin-top: 0.3rem;
    }
  }
  .detail-content {
    padding-top: 0.4rem;
    > div:last-child {
      margin-top: 0.3rem;
    }
  }
  .margin-top-50 {
    margin-top: 0.5rem;
  }
  .margin-top-40 {
    margin-top: 0.4rem;
  }
  img {
    width: 100%;
  }
}

.case-content {
  .box {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .box li {
    width: calc(50% - 0.12rem);
    background: #f8f8f8;
    margin: 0.2rem 0;
  }
  .detail {
    padding: 0.15rem 0.2rem 0.3rem 0.28rem;
    color: #3f4a82;
    .desc {
      p:first-child {
        font-size: 0.22rem;
      }
      p:last-child {
        margin-top: 0.15rem;
        font-size: 0.28rem;
      }
    }
    .pv {
      margin-top: 0.34rem;
      font-size: 0.22rem;
      position: relative;
      padding-bottom: 0.1rem;
    }
    .pv::before {
      position: absolute;
      bottom: 0;
      left: 0;
      content: "";
      width: 0.62rem;
      height: 0.02rem;
      background: #3f4a82;
    }
  }
}
</style>



